<template>
    <div>
        <div id="container" class="mymap" :style="style"></div>
    </div>
</template>

<script>
// import remoteLoad from '../assets/js/remoteLoad'
// import { MapKey } from '../config/config'
import AMap from 'AMap'
export default {
  name: 'GMap',
  data () {
    return {
      windowsHeight: 0,
      windowsWidth: 0,
      style: `width:${window.innerWidth}px;height: ${window.innerHeight}px;`,
      AMapUI: null,
      AMap: null
    }
  },
  methods: {
    initMap () {
      // eslint-disable-next-line no-unused-vars
      const that = this
      /* 构造地图实例 */
      // let AMap = that.AMap = window.AMap

      // eslint-disable-next-line no-unused-vars
      let map = new AMap.Map('container', {
        /*
          * 不指定地图中间点,而是自动定位
          */
        zoom: 11, // 级别
        // center: [116.397428, 39.90923], // 中心点坐标
        resizeEnable: true,
        viewMode: '3D'// 使用3D视图
      })

      AMap.plugin(['AMap.Scale', 'AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.DistrictSearch'], function () { // 异步同时加载多个插件
        /*
        * 添加工具栏
        * 'AMap.ToolBar',
         const toolbar = new AMap.ToolBar()
         map.addControl(toolbar)
         */

      })
    },
    /* 当窗口变化时,实时改变窗口大小 */
    changeWindow () {
      this.windowsWidth = window.innerWidth
      this.windowsHeight = window.innerHeight
    },
    scrollToTop () {
      /* 获取当前滚动距离 */
      // eslint-disable-next-line no-unused-vars
      const pageRollingNow = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
    }
  },
  mounted () {
    const that = this
    /* 添加监听滚动事件 */
    window.addEventListener('scroll', this.scrollToTop)
    /* 添加监听窗口变化事件 */
    window.addEventListener('resize', this.changeWindow)
    /* 获取窗口高度和宽度 */
    window.onresize = () => {
      return (() => {
        that.style = `width:${window.innerWidth}px;height: ${window.innerHeight}px;`
      })()
    }
    that.initMap() // 加载地图和相关组件
  },
  created () {
    this.windowsWidth = window.innerWidth
    this.windowsHeight = window.innerHeight
  }

}
</script>
<style>
    .mymap{
        width: 300px;
        height: 300px;
    }
</style>



